/* ==========================================================================
   Horizontal Scroll Prevention -    
         
   ========================================================================== */

/* ===== GLOBAL HORIZONTAL SCROLL PREVENTION ===== */
/*     */

/*   */
html, body {
  overflow-x: hidden !important;
  max-width: 100vw !important;
  box-sizing: border-box;
}

/*    */
*,
*::before,
*::after {
  max-width: 100%;
  box-sizing: border-box;
}

/*   */
.container,
.content,
.wrapper,
.section,
.page,
.main,
.header,
.footer,
.nav,
.navbar,
main,
header,
footer,
nav,
article,
section,
aside,
div {
  overflow-x: hidden;
  max-width: 100%;
  box-sizing: border-box;
}

/* ===== RESPONSIVE MEDIA ELEMENTS ===== */
/*    */

/*  */
img,
picture,
video,
canvas,
svg,
iframe,
embed,
object {
  max-width: 100% !important;
  height: auto;
  display: block;
  box-sizing: border-box;
}

/*   */
.image-container,
.img-container,
.video-container,
.media-container {
  overflow: hidden;
  max-width: 100%;
  box-sizing: border-box;
}

/* ===== TABLE RESPONSIVE HANDLING ===== */
/*    */

/*   */
.table-container {
  overflow-x: auto;
  max-width: 100%;
  margin: 1rem 0;
  border-radius: 8px;
  border: 1px solid var(--border-primary, #e5e7eb);
}

/*   */
table {
  width: 100%;
  max-width: 100%;
  border-collapse: collapse;
  table-layout: auto;
}

/*    */
@media (max-width: 768px) {
  table {
    font-size: 0.875rem;
    min-width: 100%;
  }
  
  th, td {
    padding: 0.5rem;
    white-space: nowrap;
  }
  
  /*      */
  .table-cell-wrap {
    white-space: normal;
    word-break: break-word;
    min-width: 120px;
  }
}

/* ===== FORM ELEMENTS ===== */
/*   */

form,
.form,
.form-group,
.form-row {
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

input,
textarea,
select,
button {
  max-width: 100%;
  box-sizing: border-box;
}

/*  URL   */
input[type="url"],
input[type="email"],
textarea {
  word-break: break-all;
  overflow-wrap: break-word;
}

/* ===== GRID AND FLEX LAYOUTS ===== */
/*     */

/*   */
.grid,
.grid-container,
[class*="grid-"],
[class*="col-"] {
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

/*   */
.flex,
.flex-container,
.d-flex,
[class*="flex-"] {
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

/*     */
@media (max-width: 768px) {
  .flex,
  .d-flex {
    flex-wrap: wrap;
  }
}

/* ===== TEXT CONTENT ===== */
/*   */

/*    */
p,
h1, h2, h3, h4, h5, h6,
span,
div,
li,
td, th,
a,
label {
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
  max-width: 100%;
}

/*   */
pre,
code {
  overflow-x: auto;
  max-width: 100%;
  white-space: pre-wrap;
  word-break: break-all;
}

/* ===== CARD COMPONENTS ===== */
/*   */

.card,
.card-container,
[class*="card-"] {
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

.card-body,
.card-content {
  overflow-x: hidden;
  word-wrap: break-word;
}

/* ===== BUTTON GROUPS ===== */
/*   */

.btn-group,
.button-group,
.buttons {
  max-width: 100%;
  overflow-x: auto;
  white-space: nowrap;
}

@media (max-width: 768px) {
  .btn-group,
  .button-group,
  .buttons {
    flex-direction: column;
    white-space: normal;
  }
  
  .btn-group .btn,
  .button-group .btn,
  .buttons .btn {
    width: 100%;
    margin: 0.25rem 0;
  }
}

/* ===== NAVIGATION ===== */
/*  */

.navbar,
.nav,
.navigation,
.menu {
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

.navbar-nav,
.nav-list,
.menu-list {
  max-width: 100%;
  overflow-x: auto;
}

@media (max-width: 768px) {
  .navbar-nav,
  .nav-list,
  .menu-list {
    flex-direction: column;
    overflow-x: visible;
  }
}

/* ===== MODAL AND OVERLAY ===== */
/*    */

.modal,
.overlay,
.popup {
  max-width: 100vw;
  overflow-x: hidden;
  box-sizing: border-box;
}

.modal-content,
.modal-body {
  max-width: 100%;
  overflow-x: hidden;
  word-wrap: break-word;
}

/* ===== UTILITY CLASSES ===== */
/*   */

/*      */
.no-horizontal-scroll {
  overflow-x: hidden !important;
  max-width: 100% !important;
}

/*   */
.responsive-container {
  max-width: 100%;
  overflow-x: hidden;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}

/*    */
.text-break {
  word-wrap: break-word !important;
  overflow-wrap: break-word !important;
  word-break: break-word !important;
  hyphens: auto !important;
}

/*    */
.text-korean {
  word-break: keep-all;
  overflow-wrap: break-word;
  hyphens: none;
}

/* ===== PRINT STYLES ===== */
/*   */

@media print {
  *,
  *::before,
  *::after {
    overflow: visible !important;
    max-width: none !important;
  }
  
  .no-print {
    display: none !important;
  }
}

/* ===== EMERGENCY FIXES ===== */
/*    */

/*        */
[style*="width"] {
  max-width: 100% !important;
}

/*       */
[style*="px"],
[style*="vw"],
[style*="vh"] {
  max-width: 100vw !important;
}

/*    */
.position-absolute,
.absolute,
[style*="position: absolute"] {
  max-width: calc(100vw - 2rem);
  right: 1rem;
}

/*    */
.position-fixed,
.fixed,
[style*="position: fixed"] {
  max-width: 100vw;
  left: 0;
  right: 0;
}

/* ===== DEBUGGING ===== */
/*  */

/*        */
@media screen and (max-width: 9999px) {
  .debug-horizontal-scroll * {
    outline: 1px solid red;
  }
  
  .debug-horizontal-scroll *:hover {
    background-color: rgba(255, 0, 0, 0.1);
  }
}