/* ==========================================================================
   Korean Layout Optimization CSS
      -  ,   ,  
   ========================================================================== */

/* ===== 1.      ===== */

/*     */
.emoji-text-container {
  display: flex;
  align-items: center;
  gap: var(--space-2, 0.5rem);
  word-break: keep-all;
}

/*   -      */
.emoji-icon {
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1.2em;
  height: 1.2em;
  font-size: inherit;
  line-height: 1;
  vertical-align: middle;
}

/*   */
.emoji-icon--lg {
  width: 1.5em;
  height: 1.5em;
  font-size: 1.25em;
}

/*   */
.emoji-icon--sm {
  width: 1em;
  height: 1em;
  font-size: 0.875em;
}

/*   -   */
.emoji-text {
  flex: 1;
  word-break: keep-all;
  line-height: var(--line-height-korean-body, 1.7);
  letter-spacing: var(--letter-spacing-normal, -0.005em);
}

/* ===== 2.      ===== */

/*    */
.korean-text-block {
  word-break: keep-all;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: none;
}

/*    -    */
.korean-title-block {
  word-break: keep-all;
  line-height: var(--line-height-korean-title, 1.3);
  letter-spacing: var(--letter-spacing-tighter, -0.03em);
  margin-bottom: var(--space-4, 1rem);
}

/*    */
.korean-title-responsive {
  font-size: clamp(1.25rem, 4vw, 2rem);
  line-height: var(--line-height-korean-title, 1.3);
  word-break: keep-all;
  text-align: center;
}

@media (max-width: 768px) {
  .korean-title-responsive {
    font-size: clamp(1.125rem, 5vw, 1.5rem);
    line-height: 1.4;
    text-align: left;
  }
}

/* ===== 3.     ===== */

/*   */
.service-card {
  display: flex;
  flex-direction: column;
  gap: var(--space-3, 0.75rem);
  padding: var(--space-6, 1.5rem);
  border-radius: var(--radius-lg, 0.75rem);
  background: var(--color-gray-0, #ffffff);
  border: 1px solid var(--color-gray-200, #e5e7eb);
  transition: var(--transition-all, all 150ms ease);
}

.service-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  border-color: var(--color-brand-300, #a8a8ff);
}

/*   -   */
.service-card__header {
  display: flex;
  align-items: center;
  gap: var(--space-3, 0.75rem);
  margin-bottom: var(--space-2, 0.5rem);
}

.service-card__icon {
  flex-shrink: 0;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  background: var(--color-brand-50, #f5f5ff);
  border-radius: var(--radius-md, 0.5rem);
}

.service-card__title {
  flex: 1;
  font-size: var(--font-size-lg, 1.125rem);
  font-weight: var(--font-weight-semibold, 600);
  line-height: var(--line-height-korean-title, 1.3);
  word-break: keep-all;
  color: var(--color-gray-900, #111827);
}

/*   */
.service-card__description {
  font-size: var(--font-size-sm, 0.875rem);
  line-height: var(--line-height-korean-body, 1.7);
  color: var(--color-gray-600, #4b5563);
  word-break: keep-all;
  margin-bottom: var(--space-4, 1rem);
}

/*    */
.service-card__action {
  margin-top: auto;
  padding: var(--space-3, 0.75rem) var(--space-4, 1rem);
  background: var(--color-brand-500, #5c5ce0);
  color: white;
  border: none;
  border-radius: var(--radius-md, 0.5rem);
  font-weight: var(--font-weight-medium, 500);
  word-break: keep-all;
  text-align: center;
  text-decoration: none;
  transition: var(--transition-colors);
  cursor: pointer;
}

.service-card__action:hover {
  background: var(--color-brand-600, #4646d3);
}

/* ===== 4.    ===== */

/*   */
.result-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: var(--space-8, 2rem);
  gap: var(--space-4, 1rem);
}

.result-header__icon {
  width: 4rem;
  height: 4rem;
  font-size: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-brand-50, #f5f5ff);
  border-radius: var(--radius-full, 9999px);
  flex-shrink: 0;
}

.result-header__title {
  font-size: var(--font-size-2xl, 1.5rem);
  font-weight: var(--font-weight-semibold, 600);
  line-height: var(--line-height-korean-title, 1.3);
  word-break: keep-all;
  color: var(--color-gray-900, #111827);
}

.result-header__subtitle {
  font-size: var(--font-size-base, 1rem);
  line-height: var(--line-height-korean-body, 1.7);
  color: var(--color-gray-600, #4b5563);
  word-break: keep-all;
  max-width: 32rem;
}

/* ===== 5.    ===== */

/*    */
.korean-list-item {
  display: flex;
  align-items: flex-start;
  gap: var(--space-3, 0.75rem);
  padding: var(--space-4, 1rem);
  border-radius: var(--radius-md, 0.5rem);
  background: var(--color-gray-50, #f9fafb);
  margin-bottom: var(--space-3, 0.75rem);
}

.korean-list-item__icon {
  flex-shrink: 0;
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.125rem; /*     */
}

.korean-list-item__content {
  flex: 1;
  word-break: keep-all;
  line-height: var(--line-height-korean-body, 1.7);
}

.korean-list-item__title {
  font-weight: var(--font-weight-medium, 500);
  color: var(--color-gray-900, #111827);
  margin-bottom: var(--space-1, 0.25rem);
  word-break: keep-all;
}

.korean-list-item__description {
  font-size: var(--font-size-sm, 0.875rem);
  color: var(--color-gray-600, #4b5563);
  word-break: keep-all;
  line-height: var(--line-height-korean-body, 1.7);
}

/* ===== 6.    -   ===== */

/*    */
.korean-grid {
  display: grid;
  gap: var(--space-6, 1.5rem);
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

@media (max-width: 640px) {
  .korean-grid {
    grid-template-columns: 1fr;
    gap: var(--space-4, 1rem);
  }
}

/* 2  -   */
.korean-grid--2 {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
}

@media (max-width: 768px) {
  .korean-grid--2 {
    grid-template-columns: 1fr;
  }
}

/* 3  -   */
.korean-grid--3 {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

@media (max-width: 1024px) {
  .korean-grid--3 {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  }
}

@media (max-width: 768px) {
  .korean-grid--3 {
    grid-template-columns: 1fr;
  }
}

/* ===== 7.    ===== */

@media (max-width: 768px) {
  /*     */
  .korean-title-responsive {
    font-size: clamp(1.125rem, 5vw, 1.375rem);
  }

  /*     */
  .service-card {
    padding: var(--space-4, 1rem);
  }

  /*     */
  .korean-list-item {
    padding: var(--space-3, 0.75rem);
    gap: var(--space-2, 0.5rem);
  }

  /*     */
  .result-header__icon {
    width: 3rem;
    height: 3rem;
    font-size: 2rem;
  }

  .result-header__title {
    font-size: var(--font-size-xl, 1.25rem);
  }
}

/* ===== 8.     ===== */

/*    */
.service-card:focus-within,
.korean-list-item:focus-within {
  outline: 2px solid var(--color-brand-500, #5c5ce0);
  outline-offset: 2px;
}

/*     */
.service-card__action,
button,
.btn {
  min-height: 44px;
  min-width: 44px;
}

/*    */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* ===== 9.   ===== */

@media print {
  .service-card,
  .korean-list-item {
    break-inside: avoid;
    page-break-inside: avoid;
  }

  .korean-title-block {
    color: black !important;
  }

  .emoji-icon {
    -webkit-print-color-adjust: exact;
    color-adjust: exact;
  }
}
